// Initialisation des boutons de langue et du bouton de fermeture
document.addEventListener('DOMContentLoaded', () => {
    const langEnBtn = document.getElementById("lang-en");
    const langFrBtn = document.getElementById("lang-fr");
    const closeBentoBtn = document.getElementById("close-bento");
    const aboutContainer = document.querySelector(".about-container");

    // Gestion des langues
    if (langEnBtn && langFrBtn) {
        langEnBtn.addEventListener("click", () => {
            document.getElementById("quote").innerHTML = content.en.quote;
            document.getElementById("welcome-message").innerHTML = content.en.welcome;
            document.getElementById("description").innerHTML = content.en.description;
        });

        langFrBtn.addEventListener("click", () => {
            document.getElementById("quote").innerHTML = content.fr.quote;
            document.getElementById("welcome-message").innerHTML = content.fr.welcome;
            document.getElementById("description").innerHTML = content.fr.description;
        });
    }

    // Gestion du bouton de fermeture
    if (closeBentoBtn && aboutContainer) {
        closeBentoBtn.addEventListener("click", () => {
            aboutContainer.classList.toggle("hidden");
            closeBentoBtn.textContent = aboutContainer.classList.contains("hidden") ? "+" : "×";
        });
    }

    // Animation des cartes de compétences
    document.querySelectorAll('.bento-item').forEach(item => {
        item.addEventListener('mouseenter', () => {
            item.style.transform = 'translateY(-10px) scale(1.02)';
        });

        item.addEventListener('mouseleave', () => {
            item.style.transform = 'translateY(0) scale(1)';
        });
    });

    // Mouvement de caméra basé sur la position de la souris
    let mouseX = 0;
    let mouseY = 0;
    let targetX = 0;
    let targetY = 0;
    let time = 0;

    document.addEventListener('mousemove', (event) => {
        mouseX = (event.clientX - window.innerWidth / 2) * 0.002;
        mouseY = (event.clientY - window.innerHeight / 2) * 0.002;
    });

    function updateCamera() {
        if (!window.threeAnimation || !window.threeAnimation.camera) return;

        time += 0.01;
        
        // Mouvement fluide vers la position de la souris
        targetX += (mouseX - targetX) * 0.05;
        targetY += (mouseY - targetY) * 0.05;

        // Ajout d'un léger mouvement organique
        const organicX = Math.sin(time * 0.5) * 0.3;
        const organicY = Math.cos(time * 0.4) * 0.2;

        // Combinaison du mouvement de la souris et du mouvement organique
        window.threeAnimation.camera.position.x += (targetX * 100 + organicX - window.threeAnimation.camera.position.x) * 0.05;
        window.threeAnimation.camera.position.y += (-targetY * 100 + organicY - window.threeAnimation.camera.position.y) * 0.05;
        
        // Maintenir le regard vers le centre avec un léger décalage
        window.threeAnimation.camera.lookAt(
            targetX * 20,  // Décalage du point de focus
            -targetY * 20,
            0
        );

        requestAnimationFrame(updateCamera);
    }

    updateCamera();
}); 